header {
  width: 80%;
  margin: auto;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.logo-large {
  display: none;
}

.menu {
  display: none;
  /* Oculto por defecto en pantallas peque√±as */
  flex-direction: column;
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.menu li {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  text-transform: uppercase;
  font-size: 19px;
  letter-spacing: 0.2em;
  text-align: center;
  margin: 10px 0;
}

.menu a {
  color: white;
  text-decoration: none;
  padding: 8px 16px;
}

.menu a:hover {
  color: #00e0ff;
}

.menu-toggle {
  display: none;
}

.menu-icon {
  display: block;
  font-size: 26px;
  color: white;
  cursor: pointer;
}

.menu-toggle:checked+.menu-icon+.menu {
  display: flex;
}

@media screen and (min-width: 768px) {}

@media (min-width: 768px) {
  .menu {
    display: flex;
    flex-direction: row;
    width: auto;
  }

  .menu li {
    margin: 0 10px;
  }

  .menu-icon {
    display: none;
  }
}


