}

.dropdown-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.dropdown-menu {
  display: block;
  margin-top: 10px;
  position: absolute;
  background-color: #000000;
  min-width: 160px;
  box-shadow: 0px 2px 25px 0px rgba(0, 224, 255, 0.5);
  z-index: 1;
  border-radius: 5px;
  right: 0;
  list-style: none;
  padding-left: 0;
}

.dropdown-menu li {
  list-style: none;
}

.dropdown-menu li a {
  color: white;
  padding: 12px 0px;
  margin: 0;
  text-decoration: none;
  display: block;
}

.nav-logo {
  margin-right: 10px;
}

.dropdown-menu hr {
  height: 1px;
  background: linear-gradient(to right, #38383800, #00e0ff, #38383800);
  border: none;
  margin: 0 auto;
  width: 60%;
}