article {
  color: #fff;
}

article section h1.title-season {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  text-transform: uppercase;
  font-size: 50px;
  margin: 0;
  text-align: center;
}

article section p.text-season {
  font-family: "Raleway Regular", "Arial Regular", sans-serif;
  font-size: 1.5rem;
  text-align: center;
  margin: 3% 5%;
}

.container-infoPass {
  margin-top: 30px;
}

.container-infoSesion h1.title-season,
.container-leaderboard h1.title-season {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  text-transform: uppercase;
  font-size: 45px;
  margin: 0;
  margin-bottom: -10px;
  letter-spacing: 0.1em;
  text-align: center;
}

.container-sandPool {
  margin-top: 30px;
  text-align: center;
}

.container-barProgress {
  position: relative;
  display: flex;
  justify-content: center;
  margin: 100px 0px 50px;
}
.container-barProgress + hr {
  position: absolute;
  width: 1060px;
  left: 47.9%;
  margin-top: -160px;
  transform: translateX(-50%);
  background: rgb(0, 224, 255);
  background: linear-gradient(
    90deg,
    rgba(0, 224, 255, 0.14) 14%,
    rgba(0, 224, 255, 0.8) 73%
  );
  z-index: 1;
}

.container-barProgress .info-pool {
  font-family: "Raleway Semibold Italic", "Helvetica Semibold Italic",
    "Arial Semibold Italic", sans-serif;
  margin: 0;
  margin-right: 10px;
  width: 100px;
  text-align: end;
}
.container-barProgress .initial-bar {
  position: absolute;
  margin-left: -59.9%;
  margin-top: 10px;
  z-index: 1;
}
.container-barProgress .container-barProgress2 {
  position: relative;
  display: flex;
  justify-content: center;
}

.container-barProgress .backgorund-barProgress {
  position: absolute;
  left: 47.9%;
  top: -120%;
  transform: translate(-50%, 50%);
  width: 1060px;
}
.container-reward {
  position: relative;
  margin-left: 15px;
  margin-top: -70px;
}
.container-reward .backgorund-reward {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.container-reward .coin-reward {
  position: relative;
  z-index: 1;
}
.container-reward p {
  font-family: "Ralewey Bold", "Helvetica Semibold", "Arial semibold",
    sans-serif;
  color: #ffd540;
  font-size: 21px;
  text-align: left;
  filter: drop-shadow(0px 0px 9px rgb(255, 199, 0, 0.89));
  margin-top: -35px;
  position: relative;
  margin-left: 40px;
  z-index: 1;
}

.container-infoReward {
  position: relative;
  width: 100%;
}
.container-infoReward .backgorund-infoReward {
  position: relative;
}

.container-infoReward p {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-family: "Ralewey Bold", "Helvetica Semibold", "Arial semibold",
    sans-serif;
  font-size: 25px;
  color: #fff;
  width: 45%;
  text-align: center;
  margin: auto;
}
.container-infoReward .chest-infoReward {
  position: absolute;
  right: 60;
  top: 50%;
  transform: translate(-50%, -50%);
  filter: drop-shadow(0px 0px 20.7px rgba(255, 199, 0, 0.14));
}
.container-infoReward p span {
  color: #ffd540;
}
.txt-invite {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-weight: bold;
  font-size: 38px;
  margin: 37px 0 55px 0;
  padding: 0;
}

.container-info-benefits {
  position: relative;
}
.container-info-benefits .backgorund-info-benefits {
  position: absolute;
  right: 0;
  top: 0;
  width: auto;
  transform: translate(0%, -35%);
  clip-path: 50%;
}

.container-info {
  position: relative;
  display: flex;
  justify-content: center;
  margin-top: 100px;
  margin-bottom: 74px;
}

.backgorund-infoReward2 {
  position: relative;
  width: fit-content;
}

.container-infoSeason {
  display: flex;
  width: 75%;
  margin: 0 auto;
  justify-content: center;
  gap: 0 30px;
  text-align: center;
  position: absolute;
  top: 0;
  padding: 49px 0 0 0;
}

.container-infoSeason .subtitle-season {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-weight: bold;
  font-size: 47px;
  padding: 25px 0 0 0;
  margin: 0;
}

.container-infoSeason ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container-infoSeason .container-benefits {
  width: 100%;
}

.container-infoSeason .container-benefits ul li {
  text-align: left;
  margin-left: 55px;
  font-family: "Raleway Semibold", "Helvetica Semibold", "Arial semibold",
    sans-serif;
  font-size: 18px;
  margin-top: 39px;
}

.container-infoSeason .container-benefits ul li + hr {
  background: rgb(0, 224, 255);
  background: linear-gradient(
    90deg,
    rgba(0, 224, 255, 0) 0%,
    rgba(0, 224, 255, 1) 100%
  );
  margin: 29px 0 0 0;
  height: 0.9px;
}

.container-infoSeason .element-free {
  width: 100%;
  position: relative;
  border-radius: 12px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25))
    drop-shadow(0px 0px 60px rgba(0, 54, 135, 0.4));
}

.container-infoSeason .element-free::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 0.5px solid transparent;
  background: linear-gradient(
    29deg,
    rgba(59, 191, 240, 1) 0%,
    rgba(0, 39, 56, 0.15) 100%
  );
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  pointer-events: none;
  box-sizing: border-box;
  border-radius: 12px;
}

.container-infoSeason .content-free {
  border-radius: 12px;
  background-color: rgba(0, 15, 38, 0.6);
  backdrop-filter: blur(17.9px);
  position: relative;
  z-index: 1;
  height: 100%;
}

.container-infoSeason .content-free ul li,
.container-infoSeason .content-pass ul li {
  font-family: "Raleway Regular", "Arial Regular", sans-serif;
  font-size: 18px;
  text-align: center;
  margin-top: 39px;
  position: relative;
}

.container-infoSeason .content-free ul li hr {
  width: 80%;
  background: rgb(56, 56, 56);
  background: linear-gradient(
    90deg,
    rgba(56, 56, 56, 0) 0%,
    rgba(0, 224, 255, 1) 50%,
    rgba(56, 56, 56, 0) 100%
  );
  margin: 29px auto 0 auto;
}

.container-infoSeason .element-pass {
  width: 100%;
  position: relative;
  border-radius: 12px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25))
    drop-shadow(0px 0px 60px rgba(158, 0, 255, 0.4));
}

.container-infoSeason .element-pass::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 1.5px solid transparent;
  background: rgb(158, 0, 255);
  background: linear-gradient(
    90deg,
    rgba(158, 0, 255, 1) 0%,
    rgba(0, 255, 255, 0.15) 100%
  );
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  pointer-events: none;
  box-sizing: border-box;
  border-radius: 12px;
}

.container-infoSeason .content-pass {
  border-radius: 12px;
  background-color: rgba(21, 0, 41, 0.6);
  backdrop-filter: blur(17.9px);
  position: relative;
  z-index: 1;
  height: 100%;
}

.container-infoSeason .content-pass .subtitle-season {
  padding: 25px 0 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0px 16px;
}

.container-infoSeason .content-pass .subtitle-season h2 {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-weight: bold;
  font-size: 47px;
  margin: 0;
  padding: 0;
}

.container-infoSeason .content-pass .subtitle-season .subtitle-start {
  filter: drop-shadow(0px 0px 11.2px rgba(255, 176, 57, 0.77));
  margin: 0;
}

.container-infoSeason .content-pass ul li hr {
  width: 80%;
  background: rgb(56, 56, 56);
  background: linear-gradient(
    90deg,
    rgba(56, 56, 56, 0) 0%,
    rgba(156, 95, 255, 1) 50%,
    rgba(56, 56, 56, 0) 100%
  );
  margin: 29px auto 0 auto;
}

.shadow {
  position: absolute;
  left: 0;
  bottom: 0;
}

.btn-info {
  position: absolute;
  margin: 0 30%;
  z-index: 2;
  transform: translate(0%, -50%);
}

.float-turtle {
  position: absolute;
  width: fit-content;
  z-index: 0;
  bottom: -26%;
  right: 0;
  filter: drop-shadow(0px 0px 60px rgba(158, 0, 255, 0.51));
}

