article.container-quest h3 {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-size: 28px;
  margin: 0;
  padding: 0;
  margin-bottom: 5px;
}

article.container-quest > div {
  width: 700px;
  position: relative;
  border-radius: 11px;
  z-index: 0;
  display: flex;
  justify-content: center;
}

article.container-quest > div::before {
  content: "";
  position: absolute;
  width: 700px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 11px;
  padding: 1px;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  z-index: -1;
}

.todo {
  box-shadow: 0px 0px 26px rgba(0, 209, 255, 0.33);
  background: rgb(0, 0, 0);
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 18, 32, 1) 100%
  );
}

.todo::before {
  background: linear-gradient(
    to bottom,
    rgba(59, 207, 240, 1) 0%,
    rgba(0, 209, 255, 0) 81%
  );
}

.in-review {
  box-shadow: 0px 0px 26px rgba(240, 102, 59, 0.33);
  background: rgb(0, 0, 0);
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 0%,
    rgba(32, 8, 0, 1) 100%
  );
}

.in-review::before {
  background: linear-gradient(
    to bottom,
    rgba(240, 102, 59, 1) 0%,
    rgba(240, 102, 59, 0) 81%
  );
}

.not-approved {
  box-shadow: 0px 0px 26px rgba(255, 0, 0, 0.33);
  background: rgb(0, 0, 0);
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 0%,
    rgba(32, 0, 0, 1) 100%
  );
}

.not-approved::before {
  background: linear-gradient(
    to bottom,
    rgba(240, 59, 59, 1) 0%,
    rgba(255, 0, 0, 0) 81%
  );
}

.completed {
  box-shadow: 0px 0px 26px rgba(57, 255, 113, 0.33);
  background: rgb(0, 0, 0);
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 32, 22, 1) 100%
  );
}

.completed::before {
  background: linear-gradient(
    to bottom,
    rgba(12, 154, 0, 1) 0%,
    rgba(0, 255, 26, 0) 81%
  );
}

.container-questGame {
  width: 20%;
  margin-top: 11px;
  text-align: center;
}
.container-questGame img {
  width: fit-content;
}

.vertical-separator {
  height: 152px;
  width: 1px;
  opacity: 0.35;
}

.vs-todo {
  background: linear-gradient(
    180deg,
    rgba(0, 224, 255, 0) 0%,
    rgba(0, 224, 255, 1) 30%,
    rgba(0, 224, 255, 0.89) 51%,
    rgba(0, 224, 255, 0) 100%
  );
}
.vs-in-review {
  background: linear-gradient(
    180deg,
    rgba(240, 102, 59, 0) 0%,
    rgba(240, 102, 59, 1) 30%,
    rgba(240, 102, 59, 0.89) 51%,
    rgba(240, 102, 59, 0) 100%
  );
}
.vs-not-approved {
  background: linear-gradient(
    180deg,
    rgba(255, 0, 0, 0) 0%,
    rgba(255, 0, 0, 1) 30%,
    rgba(255, 0, 0, 0.89) 51%,
    rgba(255, 0, 0, 0) 100%
  );
}
.vs-completed {
  background: linear-gradient(
    180deg,
    rgba(57, 255, 113, 0) 0%,
    rgba(57, 255, 113, 1) 30%,
    rgba(57, 255, 113, 0.89) 51%,
    rgba(57, 255, 113, 0) 100%
  );
}

.container-questInfo {
  width: 79%;
  margin: 11px 17px;
}
.container-questInfo h4 {
  font-family: "Ralewey Bold", "Helvetica Semibold", "Arial semibold",
    sans-serif;
  font-size: 18px;
  margin: 0;
}

.container-questInfo p {
  font-family: "Raleway Italic", "Helvetica Italic", "Arial Italic", Arial,
    sans-serif;
  font-size: 13px;
  margin: 9px 0;
}

.container-questInfo p.text-completed {
  color: #39ff71;
}

.container-questInfo p.text {
  color: #00e0ff;
}

.container-questInfo .container-deadline {
  display: flex;
  column-gap: 11px;
  align-items: center;
}

.container-questInfo .container-deadline img {
  height: fit-content;
}
.container-questInfo .container-deadline img.deadline-completed {
  filter: drop-shadow(0px 0px 5.8px rgba(57, 255, 113, 0.75));
}

.container-questInfo .container-deadline img.deadline {
  filter: drop-shadow(0px 0px 5.8px rgba(255, 152, 57, 0.75));
}

.container-deadline p {
  font-family: "Raleway Light Italic", "Helvetica Italic", "Arial Italic", Arial,
    sans-serif;
  font-size: 13px;
}
.container-deadline p span {
  font-family: "Raleway Semibold Italic", "Helvetica Semibold Italic",
    "Arial Semibold Italic", sans-serif;
}

.txt-reward {
  display: flex;
  align-items: center;
}

.txt-reward p {
  font-family: "Ralewey Bold", "Helvetica Semibold", "Arial semibold",
    sans-serif;
  color: #ffb039;
  font-size: 16px;
}
.txt-reward img {
  width: auto;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.txt-reward span {
  font-family: "Ralewey Bold", "Helvetica Semibold", "Arial semibold",
    sans-serif;
  color: #00ffe0;
  font-size: 16px;
}

.container-questInfo .txt-in-review {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-size: 36px;
  font-weight: bold;
  color: #f0663b;
  letter-spacing: 1px;
  margin: 0;
}

.container-questInfo .txt-completed {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-size: 36px;
  font-weight: bold;
  color: #39ff71;
  letter-spacing: 1px;
  margin: 0;
}

.container-questInfo .txt-appoved {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: #ff3d3d;
  letter-spacing: 1px;
  margin: 0;
}

.container-questInfo .container-todo,
.container-questInfo .container-not-approved {
  display: flex;
  margin: 0;
}

.container-questInfo .container-todo input {
  width: 407px;
  height: 25px;
  border-radius: 8px;
  z-index: 0;
  box-shadow: 0px 0px 26px rgba(0, 163, 255, 0.33);
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 28, 32, 1) 100%
  );
  border: 1px solid rgba(59, 197, 240, 1);
  border-top: 0px;
  font-family: "Raleway Italic", "Helvetica Italic", "Arial Italic", Arial,
    sans-serif;
  font-size: 13px;
  color: #fff;
}

.container-todo button {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-size: 16px;
  color: #fff;
  background-color: #00b2ff;
  text-align: center;
  border-radius: 8px;
  width: 89px;
  border: none;
  margin: 0px auto;
  padding: 0;
}
.container-todo button:hover {
  filter: drop-shadow(0px 8px 30px rgba(0, 224, 255, 0.5));
  background-color: #42c6ff;
}

.container-todo button:active {
  background-color: #94dfff;
  filter: drop-shadow(0px 8px 30px rgb(148, 223, 255, 0.5));
}

.container-questInfo .container-not-approved input {
  width: 407px;
  height: 25px;
  border-radius: 8px;
  z-index: 0;
  box-shadow: 0px 0px 26px rgba(255, 61, 61, 0.33);
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 28, 32, 1) 100%
  );
  border: 1px solid rgba(255, 61, 61, 1);
  border-top: 0px;
  font-family: "Raleway Italic", "Helvetica Italic", "Arial Italic", Arial,
    sans-serif;
  font-size: 13px;
  color: #fff;
}

.container-not-approved button {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-size: 16px;
  color: #fff;
  background-color: rgba(255, 61, 61, 1);
  text-align: center;
  border-radius: 8px;
  width: 89px;
  border: none;
  margin: 0px auto;
  padding: 0;
}
.container-not-approved button:hover {
  background-color: rgb(248, 83, 83);
}

.container-not-approved button:active {
  background-color: rgb(255, 134, 134);
}
