.container-quests {
  width: 73%;
  margin: 0 auto;
}

.container-quests > div {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.container-quests .container-listQuest {
  width: 100%;
  margin: 60px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px 30px;
  justify-content: center;
}

.title-quest {
  font-family: "Bebas Neue", "Rockwell condensed", Impact, sans-serif;
  font-size: 60px;
  margin: 0;
}
.title-quest hr {
  background: rgb(0, 224, 255);
  background: linear-gradient(
    90deg,
    rgba(0, 224, 255, 1) 0%,
    rgba(56, 56, 56, 0) 100%
  );
  width: 500px;
  height: 1px;
  margin: 0;
}
.filter-quest label {
  font-family: "Raleway Italic", "Helvetica Italic", "Arial Italic", Arial,
    sans-serif;
  font-size: 16px;
  margin: 0;
}
